<template>
  <div>
    <Spinner :class="{ 'fade-out': !isLoading }" />
    <div class="row justify-content-end mb-3">
      <router-link class="btn btn-primary shadow-sm" to='/admin/products/create'>
        <i class="fas fa-file mr-1"></i> 新增商品
      </router-link>
    </div>

    <div class="row shadow card">

      <table class="table vertical__middle">
        <thead class="thead-dark">
          <tr>
            <th class="text-center" scope="col">id</th>
            <th scope="col" class="text-center">Image</th>
            <th scope="col">分類</th>
            <th scope="col">品名</th>
            <th scope="col">價格</th>
            <th class="text-center" scope="col">操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="product in products" :key="product.id">
            <th class="text-center" scope="row">{{ product.id }}</th>
            <th class="text-center">
              <div class="img__box">
                <router-link
                  :to="{ name: 'products', params: { id: product.id } }"
                >
                  <img
                    class="img__box mxw__150 img-thumbnail shadow-sm"
                    :src="product.image"
                  />
                </router-link>
              </div>
            </th>
            <td>
              <span class="badge-pill badge-info vertical__middle">
                <strong>{{ product.Category.name }}</strong>
              </span>
            </td>
            <td>
              <router-link :to="{ name: 'products', params: { id: product.id } }">
                {{ product.name }}
              </router-link>
            </td>
            <td>{{ product.price }}</td>
            <td class="text-center">
              <router-link
                class="btn btn-primary btn-sm mr-2"
                :to="{
                  name: 'adminProductsEdit',
                  params: { id: product.id },
                }"
              >
                <i class="fas fa-pencil-alt"></i>
              </router-link>

              <button
                type="submit"
                class="btn btn-outline-danger btn-sm"
                :disabled="isProcessing"
                @click.prevent.stop="deleteProduct(product.id)"
              >
                <i class="fas fa-trash-alt"></i>
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>

<script>
import productsAPI from '../../apis/products'
import Spinner from '../../components/Spinner'
import { iziToast } from '../../utils/helpers'
export default {
  data() {
    return {
      products: [],
      isProcessing: false,
      isLoading: true
    }
  },
  components: {
    Spinner
  },
  methods: {
    async fetchData() {
      try {
        this.isLoading = true
        const { data } = await productsAPI.products.get()
        this.products = data.products
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.$toast.error(' ', '操作失敗，請稍後再試', iziToast.options.error)
        this.isLoading = false
      }
    },
    async deleteProduct(id) {
      try {
        this.isProcessing = true
        const { data } = await productsAPI.products.delete({ id })
        if (data.status !== 'success') {
          this.$toast.error(' ', data.message, iziToast.options.error)
          throw new Error(data.message)
        }
        this.products = this.products.filter(p => p.id !== id) 
        this.$toast.show(' ', data.message, iziToast.options.success)
        this.isProcessing = false
      } catch (err) {
        console.log(err)
        this.$toast.error(' ', '操作失敗，請稍後再試', iziToast.options.error)
      }
    },
  },
  created() {
    this.fetchData()
  },
}
</script>

<style scoped>
table td,
table th {
  vertical-align: middle;
}
</style>
